<template>
  <div class="roleMgt">
    <div class="searchView">
      用户名：
      <el-input
        size="small" style="width:140px;margin-right:20px;" clearable 
        placeholder="请输入用户名"
        v-model="search.account">
      </el-input>
      用户姓名：
      <el-input
        size="small" style="width:140px;margin-right:20px;" clearable
        placeholder="请输入用户姓名"
        v-model="search.username">
      </el-input>
      <el-button size="small" icon="el-icon-search" type="primary">查询</el-button>
    </div>
    <div class="panel">
      <div class="panel-heading">
        <span style="height:40px;line-height:40px;">用户信息列表</span>
        
        <el-input
          size="small" style="float:right;width:140px;margin-right:20px;margin-top:4px;" clearable
          placeholder="请输入搜索条件"
          v-model="searchName">
        </el-input>
      </div>
      <div class="panel-body">
        <div class="demo-table" style="padding:10px">
          <el-table border v-if="tableShow"
            :data="fileterList"
            @selection-change="handleSelectionChange"
            stripe
            v-bind:height="heigthNum"
            style="width: 100%">
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column
              prop="name"
              label="姓名"
              width="180">
            </el-table-column>
            <el-table-column
              prop="desc"
              label="备注">
            </el-table-column>
            <el-table-column label="操作" width="180" prop="btnList">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <el-pagination style="float:right;margin-top:10px;"
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="1"
      :page-sizes="[100, 200, 300, 400]"
      v-bind:page-size="size"
      layout="total, sizes, prev, pager, next, jumper"
      v-bind:total="total">
    </el-pagination>
    <div style="position:flex;bottom:0;width:100%;height:47px;left:0px;background-color:#fff;position: absolute;">
      <el-button type="primary" size="small" icon="el-icon-plus" style="margin-left: 15px;"  @click="roleAdd = true">新增</el-button>
      <el-button type="danger" size="small" icon="el-icon-error">禁用</el-button>
      <el-button type="success" size="small" icon="el-icon-success">启用</el-button>
    </div>
    <el-dialog title="新增角色" :visible.sync="roleAdd">
      <div class="rolename">
        <span>角色名：</span>
        <el-input v-model="rolename" placeholder="请输入内容" size="mini"></el-input>        
      </div>
      <el-transfer
        v-model="value3"
        filterable
        :left-default-checked="[3, 5]"
        :right-default-checked="[1, 2]"
        :render-content="renderFunc"
        :titles="['全部菜单', '角色拥有的菜单']"
        :format="{
          noChecked: '${total}',
          hasChecked: '${checked}/${total}'
        }"
        @change="handleChange"
        :data="data">
      </el-transfer>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('addRules')">确 定</el-button>
        <el-button @click="roleAdd = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'roleMgt',
  data () {
    const generateData = _ => {
      const data = []
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `备选项 ${i}`,
          disabled: i % 4 === 0
        })
      }
      return data
    }
    return {
      search: {
        account: '',
        userName: ''
      },
      alldata: [],
      maindata: [],
      searchName: '',
      heigthNum: '',
      tableShow: true,
      size: 100,
      total: 0,
      page: 1,
      roleAdd: false,
      rolename: '',
      data: generateData(),
      value3: [1, 2],
      renderFunc (h, option) {
        return <span>{ option.key } - { option.label }</span>
      }
    }
  },
  mounted () {
    let that = this
    let hei = document.querySelector('.searchView').offsetHeight
    this.heigthNum = this.$api.getHeightFun(hei)
    let resizeTimer = null
    window.onresize = function () {
      if (resizeTimer) {
        clearTimeout(resizeTimer)
      }
      resizeTimer = setTimeout(function () {
        that.tableShow = false
        that.tableShow = true
        console.log(123)
        that.heigthNum = that.$api.getHeightFun(hei)
      }, 200)
    }
    this.$api.promise({
      method: 'POST',
      url: 'demo',
      data: {'name': '12345'}
    })
    .then(res => {
      if (res.data.code > 0) {
        for (let i = 0; i < 19999; i++) {
          let datas = {'name': i + '--'}
          that.alldata.push(datas)
        }
        this.maindata = this.spliceFun(this.alldata, this.size, this.page - 1)  // 数据分页
      } else {
        this.$message.error(res.data.desc)
      }
    })
    .catch((err) => {
      this.fullscreenLoading = false
      this.$message.error('接口异常：' + err)
    })
  },
  computed: {
    fileterList: function () {
      var newdata = this.alldata.filter(list => {
        return list.name.match(this.searchName)
      })
      return this.spliceFun(newdata, this.size, this.page - 1)
    }
  },
  methods: {
    handleChange (value, direction, movedKeys) {
      console.log(value, direction, movedKeys)
    },
    spliceFun (list, size, pages) {
      this.total = list.length // 获取数据总量
      return list.slice(size * pages, size * pages + size)
    },
    handleSizeChange (val) {
      // 每页显示val条数据
      this.size = val
      this.maindata = this.spliceFun(this.alldata, this.size, this.page - 1)
    },
    handleCurrentChange (val) {
      // 跳转到第 val 页
      this.page = val
      this.maindata = this.spliceFun(this.alldata, this.size, this.page - 1)
    },
    submitForm (formName) {
      alert(1)
      this.roleAdd = false
    }
  }
}
</script>
<style lang="scss" type="text/css">
  .rolename {
    margin-bottom: 15px;
  }
  .rolename .el-input {
    display: inline-block;
    width: 30%;
  }
</style>


